<template>
    <div class="error-page">
        <div class="error-page__bd">
            <i class="error-page__bg" />
            <p>{{ title }}</p>
            <div
                class="error-page__reload"
                @click="reloadHandler" />
        </div>
    </div>
</template>
<script>
export default {
    name: 'Error',
    data() {
        return {
            title: '加载出错, 请点击重试！',
            from: null,
        };
    },
    computed: {
        fromPath() {
            return this.$router.from.fullPath;
        },
    },
    methods: {
        reloadHandler() {
            this.$root.showPlaceholder();
            this.$router.replace({ path: this.fromPath });
        },
    },
    mounted() {
        // eslint-disable-next-line func-names
        this.$nextTick(function () {
            this.$root.hidePlaceholder();
        });
        this.$o2o({ e_c: '错误页', e_a: '曝光', e_n: '想看错误页', e_x: { path: this.fromPath } });
    },
};
</script>
